/**
 * 整个项目api管理
 */
import request from "./request"

export default {
    getUserData(params){
        return request({
            url: '/user/getUser',
            method: 'get',
            mock:false,
            data:params
        })
    },
    addUser(params){
        return request({
            url: '/user/add',
            method: 'post',
            mock:false,
            data:params
        })
    },
    editUser(params){
        return request({
            url: '/user/edit',
            method: 'post',
            mock:false,
            data:params
        })
    },
    deleteUser(params){
        return request({
            url: '/user/delete',
            method: 'get',
            mock:false,
            data:params
        })
    },
    // 根据不同用户返回不同的用户菜单
    getMenu(params){
        return request({
            url: '/permission/getMenu',
            method: 'post',
            mock:false,
            data:params
        })
    },
    getRepoList(params){
        return request({
            url: '/repo/getRepoList',
            method: 'get',
            mock:false,
            data:params
        })
    },
}